.main-form__wrap {
   font-weight: 300;
   text-align: center;

   a {
      color: $color-text-module;
      border-bottom: 1px solid #fff;

      &:hover {
         opacity: .9;
      }
   }

   .help-link {
      line-height: 1.4;
   }
}

input, select, textarea {
   font-family: $default-font;
}

.main-form__title {
   @include response-val('font-size', 20, 18, $type-responsive-val);
   font-weight: 700;
   border: $light-div;
   color: #fff;
   border-radius: 4px;
   padding: 15px;
   margin-bottom: 10px;
}

select {
   cursor: pointer;
   color: $color-placholder;
}

input:not([type="submit"]),
select {
   &:focus {
      outline: 2px solid $main-color;
      box-shadow: 0 0 0.5px 4px $main-color;
   }
}
input:not([type="submit"]).no-valid,
select.no-valid {
   color: $color-accent;
   &::placeholder {
      color: $color-accent;
   }
   &:focus, &:active {
      color: $color-accent;
   }
}

// arrow select
select {
   --size: 8px;
   --color: #999;
   --position: 23px;

   appearance: none;
   background:
    linear-gradient(45deg, transparent 50%, var(--color) 50%),
    linear-gradient(135deg, var(--color) 50%, transparent 50%);
   background-position:
    calc(100% - var(--position)) calc(50% + 2px),
    calc(100% - var(--position) + var(--size)) calc(50% + 2px),
    100% 0;
   background-size:
    var(--size) var(--size),
    var(--size) var(--size),
    2.5em 2.5em;
   background-repeat: no-repeat;
}

.valid {
   color: $text-color;
}

input, textarea {
   color: $text-color;
}
input:not([type="radio"]), textarea, select {
   width: 100%;
   font-size: 1rem;
   background-color: #fff;
   padding: 15px 20px;
   border: none;
   border-radius: 4px;
   @include response-val('margin-top', 10, 5, $type-responsive-val);
   @include response-val('margin-bottom', 10, 5, $type-responsive-val);

   &::placeholder {
      transition: all $transition;
   }
   &:focus {
      &::placeholder {
         opacity: 0;
      }
   }
}

label {
   cursor: pointer;
}
input[type="radio"] {
   --size: 16px;
   width: var(--size);
   height: var(--size);
   position: relative;
   border: 1px solid #000;
   border-radius: 50%;
   margin-top: 0;
   cursor: pointer;

   &::before {
      content: '';
      display: none;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: calc(var(--size) - 5px);
      height: calc(var(--size) - 5px);
      background-color: #000;
      border-radius: 50%;
   }
   &:checked::before {
      display: block;
   }
}

.input-wrap {
   --size: 26px;
   position: relative;

   input {
      padding-right: calc(var(--size) / 2 * 4);
   }
}
.input-icon {
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   right: calc(var(--size) / 2);
   width: var(--size);
   height: var(--size);
   display: inline-flex;
   align-items: center;
   svg {
      width: 100%;
      height: 100%;
   }
}
button.input-icon {
   background: none;
   border: none;
   padding: 0;

   &.hide {
      opacity: .5;
   }
}

input[type="month"] {
   font-family: $default-font;
   padding-left: 10px;
   padding-right: 0;
   color: $color-placholder;
   cursor: pointer;

   &::-webkit-calendar-picker-indicator {
      opacity: .5;
      transform: translateX(-5px);
   }

