.preloader {
   position: fixed;
   left: 0;
   top: 0;
   right: 0;
   bottom: 0;
   overflow: hidden;
   background: #fff;
   z-index: 100001;
   transition: opacity $transition, visibility $transition;
 }
 
 .preloader__image {
   position: relative;
   top: 50%;
   left: 50%;
   width: 64px;
   height: 64px;
   margin-top: -32px;
   margin-left: -32px;
   // background: url('../img/preloader.gif') no-repeat 50% 50%; /*расположение (url) изображения gif и др. параметры*/
 }
 
 .loaded_hiding .preloader {
   opacity: 1;
 }
 
 .loaded .preloader {
   visibility: hidden;
   opacity: 0;
 }