
.payment {
   .wrapper {
      --gap: 15px;
      display: flex;
      flex-wrap: wrap;
      gap: var(--gap);
   }
   .wrapper__item {
      width: calc(50% - var(--gap)/2);

      @media only screen and (max-width : 992px){
         flex: 1 1 100%;
      }
   }
}

.payment__heading {
   text-align: center;
   @include response-val('margin-bottom', 15, 5, $type-responsive-val);
}

.payment__form {
   color: #fff;

   input[type="month"] {
      width: 100%;
   }
   .month-wrap {
      display: flex;
      align-items: center;

      label {
         padding-right: 10px;
         max-width: 50px;
         width: 100%;
      }
   }
}

.payment__price-wrap {
   --gap: 15px;
   display: flex;
   flex-wrap: wrap;
   flex-direction: column;
   gap: var(--gap);

   @media only screen and (max-width : 992px){
      flex-direction: row;
   }

   &>div:first-child {
      width: 100%;
      @media only screen and (max-width : 992px){
         width: calc(40% - var(--gap)/2);
      }
      @media only screen and (max-width : 768px){
         width: 100%;
      }
   }
   &>div:last-child {
      width: 100%;
      @media only screen and (max-width : 992px){
         width: calc(60% - var(--gap)/2);
      }
      @media only screen and (max-width : 768px){
         width: 100%;
      }
   }
}

.border-light {
   border: $light-div;
   border-radius: $radius-input;
   margin-top: 15px;
   padding: 5px 15px;
}

.payment__price-list {
   .total {
      font-weight: 700;
      border-bottom: none;
   }
}

.payment__price_title {
   padding-bottom: 0;
}  
.payment__price_number {
   border-bottom: 1px solid rgba($color: #fff, $alpha: .2);
}

.payment__radio_list {
   display: flex;
   align-items: center;
   justify-content: center;
   color: #fff;

   input[type="radio"] {
      border-color: #fff;
      margin-right: 5px;

      &::before {
         background-color: #fff;
      }
   }
}
.payment__radio_item {
   display: flex;
   align-items: center;
   padding-left: 10px;
   padding-right: 10px;
}


.payment_form-wrap {
   .input-icon {
      width: 30px;
      background-color: rgba($color: #C4C4C4, $alpha: .2);
      padding: 2px 5px;
      border-radius: 5px;
      
      svg {
         height: 20px;
      }
   }
}

.payment__btn-submit {
   margin-top: 30px;
}

.payment__dop-info {
   font-style: italic;
   font-size: rem(14);
   margin-top: 10px;
   padding-top: 10px;
   border-top: $light-div;
}

.warninng {
   font-size: rem(14);
   font-style: italic;
   line-height: 1.5;
   text-align: justify;
   
   &.bg-module {
      padding-top: 10px;
      padding-bottom: 10px;
   }
}

.bg-module {
   background: $bg-module;
   border-radius: $radius-module;
   padding: $padding-module;
   color: $color-text-module;
}