button {
   cursor: pointer;
   border: none;
   outline: none;
}

.main-btn {
   --radius: 8px;
   --position: -10px;
   @include response-val('font-size', 18, 16, $type-responsive-val);
   position: relative;
   font-weight: 500;
   max-width: 230px;
   width: 100%;
   background-color: $main-color;
   color: #fff;
   border-radius: var(--radius);
   margin-top: 10px;
   margin-bottom: 10px;
   padding: 15px;

   &::before {
      content: '';
      position: absolute;
      display: block;
      left: var(--position);
      right: var(--position);
      top: var(--position);
      bottom: var(--position);
      border-radius: calc(var(--radius) + 2px);
      border: 1px solid #fff;
      opacity: 0;
      transition: opacity $transition, left $transition, right $transition, top $transition, bottom $transition;
   }
   &:hover,
   &:focus {
      --position: -2px;

      &::before {
         opacity: 1;
      }
   }
}