.top-menu {
   --transition: .5s;
   position: relative;
   padding-left: 30px;

   @media only screen and (max-width : 768px){
      display: flex;
      justify-content: flex-end;
   }
}
.top-menu__list {
   z-index: 2;
   display: flex;
   flex-wrap: wrap;
   justify-content: flex-end;
   font-size: rem(14);

   @media only screen and (max-width : 768px){
      min-width: 290px;
      position: absolute;
      top: 35px;
      right: 0;
      background-color: #fff;
      padding: 15px;
      border-radius: $radius-input;
      border: 1px solid $color-light;
      visibility: hidden;
      opacity: 0;
      transform: translateY(50px);
      transition: opacity var(--transition), visibility var(--transition), transform .7s;
   }
}
.top-menu__list.active {
   visibility: visible;
   opacity: 1;
   transform: translateY(0px);
}
.top-menu__item {
   margin: 2.5px;

   @media only screen and (max-width : 768px){
      width: 100%;
   }
}
.top-menu__link {
   display: block;
   padding: 5px;
   color: $text-color;
   border: 1px solid #75A2C7;
   border-radius: $radius-input;
   transition: background-color $transition, color $transition;

   &:hover {
      background-color: rgba($color: #75A2C7, $alpha: 1);
      color: #fff;
   }

   @media only screen and (max-width : 768px){
      padding: 10px;
      background: $bg-module;
      color: #fff;
   }
}

.top-menu__toggle {
   --size: 40px;
   --less: 9px;
   display: none;
   z-index: 2;
   position: relative;
   right: 0;
   width: var(--size);
   height: calc(var(--size) - var(--less));
   background: $bg-module;
   padding: calc(var(--less) / 2);
   border: 1px solid $color-light;
   border-radius: $radius-input;
   overflow: hidden;
   transition: background $transition;

   span {
      --height: 4px;
      position: absolute;
      display: block;
      width: calc(100% - var(--less));
      height: var(--height);
      background-color: $text-color;
      background: #fff;
      border-radius: $radius-input;
      transition: background-color $transition, transform $transition, opacity $transition;
   }
   span:first-child {
      top: calc(var(--less) / 2);
      opacity: 1;
      transition: background-color $transition, transform var(--transition), opacity $transition;
   }
   span:nth-child(2), span:nth-child(3) {
      top: calc(50% - var(--height)/2);
   }
   span:last-child {
      bottom: calc(var(--less) / 2);
      opacity: 1;
      transition: background-color $transition, transform var(--transition), opacity $transition;
   }

   &.active {
      background: #fff;

      span {
         background: $bg-module;
      }

      span:first-child {
         transform: translateY(-10px);
         opacity: 0;
      }
      span:nth-child(2) {
         transform: rotate(45deg);
      }
      span:nth-child(3) {
         transform: rotate(-45deg);
      }
      span:last-child {
         transform: translateY(10px);
         opacity: 0;
      }
   }

   @media only screen and (max-width : 768px){
      display: block;
   }
}

.top-menu__overlay {
   z-index: 1;
   display: none;
   position: fixed;
   top: 0;
   left: 0;
   width: 100vw;
   height: 100vh;
   background: rgba($color: #000000, $alpha: .4);

   @media only screen and (max-width : 768px){
      display: block;
      visibility: hidden;
      opacity: 0;
      transition: opacity var(--transition), visibility var(--transition);
   }

   &.active {
      visibility: visible;
      opacity: 1;
   }

}