.overlay-bg {
   z-index: -1;
   position: fixed;
   left: 0;
   top: 0;
   width: 100%;
   height: 100%;
   background-image: url("../../images/main-bg.jpg");
   background-position: center center;
   background-repeat: no-repeat;
   background-size: cover;

   &::before {
      content: '';
      display: block;
      height: 100%;
      width: 100%;
      position: absolute;
      background-color: rgba($color: rgb(255, 255, 255), $alpha: 1);
      // background-color: rgb(220, 232, 235);
   }
}

.overlay-half {
   .overlay-bg::before {
      width: 50%;
   }
   footer {
      display: none;
   }
}