//var for fonts
$default-font: 'Montserrat', sans-serif; // основной шрифт, будет назначен для body
$base-font-size: 16; //px -- базовый размер шрифта, будет установлен для body

//var colors
$color-accent: #EB001B;
$text-color: #252525;
$color-placholder: rgba($text-color, .6);
$main-color: #FF6A30;
$color-light: #75A2C7;
$color-light-deep: #416995;

$bg-module: linear-gradient(#75A2C7, #416995);
$padding-module: 20px;
$radius-module: 10px;
$color-text-module: #fff;

$light-div: 1px solid rgba($color: #fff, $alpha: .2);
$color-bg-no-valid: #FDB6B6;

$radius-input: 4px;

$transition: .25s ease;

//var width for wrapper
$min-width: 320; // px
$max-width: 1170; // px

// переменная для указания на каких расширенииях будет пересчитываться адаптивное значение
// применяется в миксине response-val
// 1 -- только если ширина окна меньше контейнера
// 2 -- только если ширина окна больше контейнера
// 0 -- всегда
$type-responsive-val: 1;