.login-page {
   main {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
   }
}

.login {
   width: 100%;
   padding: 30px 0;

   .icon-logo {
      display: block;
      margin-left: auto;
      margin-right: auto;
   }

   .forgot {
      text-align: right;
      padding-right: 15px;
   }

   .main-btn {
      margin-top: 25px;
      margin-bottom: 20px;
   }
}

.login-wrap {
   max-width: 555px;
   width: 100%;
   margin-left: auto;
   margin-right: auto;
   @include response-val('padding-top', 70, 30, $type-responsive-val);
   @include response-val('padding-bottom', 50, 30, $type-responsive-val);
}

.login__heading {
   font-weight: 500;
   padding-top: 20px;
   padding-bottom: 10px;
}