.scrolltop {
   --size: 40px;
   display: block;
   width: var(--size);
   height: var(--size);
   border-radius: 5px;
   border: none;
   background: #fff;
   border: 1px solid $color-light;
   position: fixed;
   @include response-val('right', 50, 10, 0);
   bottom: 30px;
   cursor: pointer;

   &:hover {
      background: $bg-module;

      .scrolltop_icon {
         border-color: #fff;
      }
   }
}
.scrolltop_icon {
   display: block;
   position: absolute;
   top: 50%;
   left: 50%;
   width: calc(var(--size) / 3);
   height: calc(var(--size) / 3);
   border-top: 2px solid $color-light;
   border-right: 2px solid $color-light;
   transform: rotate(-45deg) translate(-20%, -50%);
}